body {
  margin: 0;
  padding: 0;
}

/* Main container*/
div.App {
  display: block;
  text-align: center;
  width: 100%;
  background-color: whitesmoke;
  overflow: hidden;
}
.App h1 {
  font-size: 25px;
  text-transform: uppercase;
  text-shadow: -0.2px 1px 0px white;
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
  color: #3936dd;
}
/*  parts Form */
div form {
  position: relative;
  top: 10vh;
  left: 50%;
  border: 4px solid black;
  width: 90%;
  height: 92vh;
  min-height: 330px;
  margin: 0;
  color: silver;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  background-image: url(/Daily-calories-counter/static/media/dark.e547a578.png); /* Made by Tia Newbury */
}

.choiceInput {
  text-shadow: -0.4px 0.2px 0.1px white;
  letter-spacing: 1px;
}

form input {
  margin-left: 2%;
  margin-top: 2%;
  background-color: silver;
}

/*Buttoon Add and Remove */

.buttonAdd {
  padding: 10px;
  margin: 10px;
  font-size: 20px;
  color: silver;
  text-shadow: 2px 1px 1px black;
  border: 1px solid black;
  border-radius: 25%;
  background-color: gray;
  box-shadow: 3px 3px 10px black inset, -3px -3px 10px black inset;
  cursor: pointer;
}
.buttonAdd:hover {
  -webkit-transform: translateY(-2px);
          transform: translateY(-2px);
  text-shadow: 2px 1px 1px black, 2px 1px 5px white;
}

.buttonRemove {
  font-size: 10px;
  color: white;
  text-shadow: 2px 1px 1px black;
  border: 1px solid black;
  border-radius: 25%;
  background-color: silver;
  box-shadow: 3px 3px 10px black inset, -3px -3px 10px black inset;
  text-align: center;
  cursor: pointer;
}

/*Container below form */
.boardScore {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: center;
          align-content: center;
  text-align: center;
  margin-top: 15vh;
}

/* Single div */

.boardScore div {
  display: -webkit-flex;
  display: flex;
  width: 90%;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-content: center;
          align-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-shrink: 1;
          flex-shrink: 1;
  background-image: url(/Daily-calories-counter/static/media/dark.e547a578.png); /* Made by Tia Newbury */
  border: solid 2px black;
  color: white;
  margin: 5% 0;
}

.boardScore div h1 {
  width: 100%;
  height: 10%;
  padding: 0;
  color: #3936dd;
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
  text-transform: uppercase;
  text-shadow: -0.4px 0.2px 0.2px white;
}
.boardScore div ul {
  width: 100%;
  list-style-type: none;
  text-align: left;
  font-size: 14px;
  padding: 4px;
  margin: 0;
}
.boardScore div ul li {
  width: 100%;
  padding: 3px 2px;
  margin-left: 4px;
}
.boardScore div ul li span {
  padding: 2px;
}

/* Color title div */

div.secondBreakfast h1 {
  color: palevioletred;
}
div.breakfast h1 {
  color: aqua;
}
div.dinner h1 {
  color: green;
}
div.snack h1 {
  color: red;
}
div.supper h1 {
  color: orangered;
}

/*Tabel in div */

.boardScore table {
  width: 100%;
  position: relative;
}
.boardScore table td,
.boardScore table th {
  border: 1px solid silver;
}

/*Tabel DailyCounter */

.dailyCounter {
  width: 90%;
  position: absolute;
  right: 50%;
  top: 0%;
  -webkit-transform: translateX(50%);
          transform: translateX(50%);
  border: 2px solid white;
  text-align: center;
  padding: 0.5%;
  color: white;
  background-image: url(/Daily-calories-counter/static/media/dark.e547a578.png); /* Made by Tia Newbury */
}
.dailyCounter table {
  width: 100%;
}

.dailyCounter td,
.dailyCounter th {
  border: 1px solid whitesmoke;
}

/*Media Queris*/

@media (min-width: 425px) {
  .boardScore div ul {
    font-size: 20px;
  }
}

@media (min-width: 500px) {
  .boardScore {
    -webkit-align-content: space-around;
            align-content: space-around;
  }
  .boardScore div {
    -webkit-flex-basis: 30%;
            flex-basis: 30%;
    width: 40%;
    height: 80%;
    -webkit-align-content: space-around;
            align-content: space-around;
  }
  .boardScore div ul {
    font-size: 11px;
  }
}
@media (min-width: 570px) {
  div form {
    height: 68vh;
    font-size: 18px;
    height: 30vh;
  }
}

@media (min-width: 768px) {
  div form {
    height: 90vh;
    font-size: 20px;
    padding: 20px;
  }
  form input {
    margin-top: 5%;
  }
  .boardScore div ul {
    font-size: 16px;
  }
}
@media (min-width: 1024px) {
  div form {
    height: 88vh;
    width: 70%;
    border: 4px solid black;
    margin: auto 0;
  }

  .boardScore div ul li span {
    padding: 4px;
  }

  .dailyCounter {
    width: 30%;
    right: 17%;
    top: 3%;
  }
}
@media (min-width: 1300px) {
  div form {
    height: 70vh;
  }
}

